import React, { Component } from 'react'
import Button from 'material-ui/Button'
import TextField from 'material-ui/TextField'
import { withStyles, createStyleSheet } from 'material-ui/styles';
import { pink } from 'material-ui/colors';

const styles = {
  labelText:{
    color: pink[300],
  }
}

class Login extends Component {
  render() {
    return (
      <div>
        <h2>Please enter your Email and password to log in.</h2>
        <TextField
          id="name"
          label="Name"
          margin="normal"
          autoFocus
          labelClassName={this.props.classes.labelText}
          InputClassName={this.props.classes.labelText}
        />
        {console.log(this.props.classes.labelText)}
        <TextField
          id="password"
          label="Password"
          type="password"
          margin="normal"
        />
      </div>
    );
  }
}

export default withStyles(createStyleSheet(styles))(Login);



